.compare {
  display: none;
  z-index: 100;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  background-color: rgba($color: var(--bg-page), $alpha: 0.5);

  &--open {
    display: block;
  }

  &__grid {
    display: grid;
    column-gap: $compare-column-gap;
    grid-template-columns: 1fr 1fr;
    left: 0;
    right: 0;
    height: 100%;

    @include onDesktop {
      column-gap: calc($compare-column-gap * 9);
    }
  }

  &__container {
    position: absolute;
    left: 0;
    right: 0;

    margin-block: 20px;
    padding-inline: 20px;
    padding-top: 20px;
    background: transparent;
    backdrop-filter: blur(10px);
    border: 1px solid var(--font-primary);
    border-radius: 20px;
    justify-content: center;
    overflow-y: scroll;
    overflow-x: hidden;
    height: 100%;
    max-height: calc(100vh - 80px);

    @include resetScrollBar;

    @include onTablet {
      margin-inline: 72px;
      max-height: calc(100vh - 40px);
    }

    @include onDesktop {
      margin-inline: 120px;
    }
  }

  &__cards {
    grid-column: 1 / -1;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "card-first card-last";
    column-gap: $compare-column-gap;

    @include onDesktop {
      column-gap: calc($compare-column-gap * 9);
    }
  }

  &__card {
    position: relative;
    left: 0;
    width: 100%;
    padding-top: 56px;
    background-color: var(--bg-compare);
    border-radius: 20px;

    &--first {
      grid-area: card-first;
    }

    &--last {
      grid-area: card-last;
    }

    &-img {
      background-repeat: no-repeat;
      background-position: bottom;
      background-size: contain;
      aspect-ratio: 1.5 / 1;

      &--last {
        transform: scaleX(-1);
      }

      margin-bottom: 20px;
    }

    &-title {
      text-align: center;
      margin-bottom: 20px;

      @include onTablet {
        font-size: 24px;
      }

      @include onDesktop {
        font-size: 32px;
      }
    }

    &-tth {
      grid-column: 1 / -1;
      text-align: center;
      justify-items: center;
      border-radius: 20px;
      padding-bottom: 20px;
    }
  }

  &__nav {
    z-index: 3;
    display: flex;
    justify-content: end;
    position: sticky;
    top: 0;
  }

  &__bar {
    display: flex;
    height: 25px;
    width: 100%;
    border: 1px solid var(--font-primary);
    border-radius: 10px;
    margin-bottom: 30px;
    overflow: hidden;

    &--first {
      height: 100%;
      width: 50%;
      background-color: rgba($color: var(--font-primary), $alpha: 0.1);
      border-radius: 10px 0 0 10px;
      border-right: 1px solid var(--font-primary);
      justify-items: center;
    }

    &--last {
      height: 100%;
      width: 50%;
      background-color: rgba($color: var(--font-primary), $alpha: 0.1);
      border-radius: 0 10px 10px 0;
      border-left: 1px solid var(--font-primary);
      justify-items: center;
    }
  }

  &__top {
    z-index: 2;
    grid-column: 1 / -1;

    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "first last";
    column-gap: $compare-column-gap;
    position: absolute;
    left: $compare-column-gap;
    right: $compare-column-gap;
    padding-top: 20px;

    @include onDesktop {
      column-gap: calc($compare-column-gap * 9);
    }
  }

  &__dropdown {
    position: relative;
    justify-items: center;

    &-top {
      display: flex;
      justify-content: end;
      width: 100%;
      max-width: 80%;
      background-color: var(--bg-dropdown);
      border-radius: 10px;
      padding: 10px;
      cursor: pointer;
      height: 36px;
      align-items: center;

      @include onTablet {
        height: auto;
      }

      &--open {
        border-radius: 10px 10px 0 0;
      }

      &-bike {
        align-content: center;
        margin-inline: auto;
        font-size: 10px;
        color: var(--font-secondary);
        font-weight: 800;

        @include onTablet {
          font-size: 20px;
        }
      }

      &-button {
        position: relative;
        right: 0;
        transform: scale(0.5);

        &::after,
        &::before {
          background-color: var(--font-secondary);
        }

        @include onTablet {
          transform: scale(1);
        }
      }
    }

    &--first {
      grid-area: first;
    }

    &--last {
      grid-area: last;
    }

    &-list {
      width: 100%;
      max-width: 80%;
      display: none;
      padding-inline: 10px;

      &--open {
        display: grid;
      }
    }

    &-item {
      &--last {
        @include onDesktop {
          &:hover {
            background: var(--bg-dropdown-grad-reverse);
          }
        }
      }

      @include noHoverOnTouch;
    }

    &-link {
      &--last {
        grid-template-columns: 1fr 162px;
      }
    }

    &-img {
      &--last {
        grid-area: last;
        transform: scaleX(-1);
      }
    }
  }

  &__tth {
    &-weights {
      position: relative;
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-areas: "left right";

      &::before {
        content: "";
        position: absolute;
        right: 0;
        top: 0;
        height: calc(100% - 1px);
        background-color: var(--left-color, rgba(155, 42, 42, 0));
        width: var(--left-width, 0%);
        grid-area: left;
        transition: width 0.25s ease, background-color 0.25s ease;
      }

      &::after {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        height: calc(100% - 1px);
        background-color: var(--right-color, rgba(23, 120, 76, 0));
        width: var(--right-width, 0%);
        grid-area: right;
        transition: width 0.25s ease, background-color 0.25s ease;
      }
    }

    &-list {
      width: 100%;
      display: grid;
      row-gap: 6px;
      text-align: center;
      min-width: 320px;
    }

    &-item {
      grid-column: 1 / -1;
      display: grid;
      grid-template-columns: 1fr 0.5fr 1fr;
      grid-template-areas: "first value last";
      font-size: 12px;
      align-items: center;
      background: #000;
      background: var(--bg-tth);

      @include onTablet {
        font-size: 18px;
      }

      @include onDesktop {
        font-size: 24px;
      }

      &::before {
        content: "";
        display: block;
        position: relative;
        grid-column: 1 / -1;
        height: 1px;
        background: #fff;
        background: var(--bg-tth-line);
      }
    }

    &-text {
      z-index: 1;
    }

    &--first {
      grid-area: first;
    }

    &--value {
      grid-area: value;
      font-weight: 700;
      padding-block: 5px;
    }

    &--last {
      grid-area: last;
    }
  }
}
