@font-face {
  font-family: Poppins;
  src: url("Poppins-Bold-700.d6f7c82b.woff2") format("woff2"), url("Poppins-Bold-700.eeb210c0.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Poppins;
  src: url("Poppins-Medium-500.db2a0f6b.woff2") format("woff2"), url("Poppins-Medium-500.933e0eed.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Poppins;
  src: url("Poppins-Regular-400.31058d7e.woff2") format("woff2"), url("Poppins-Regular-400.3802467d.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Poppins;
  src: url("Poppins-SemiBold-600.7314053d.woff2") format("woff2"), url("Poppins-SemiBold-600.a1f6e582.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

* {
  box-sizing: border-box;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

a {
  color: inherit;
  text-decoration: none;
  display: flex;
}

ul {
  list-style: none;
}

button {
  appearance: none;
  color: inherit;
  cursor: pointer;
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}

button:focus {
  outline: none;
}

form, label, input, textarea {
  background: none;
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
}

input, textarea {
  appearance: none;
}

textarea {
  resize: none;
}

.button {
  background-color: var(--font-primary);
  width: 100%;
  color: var(--font-secondary);
  border: 1px solid var(--font-primary);
  border-radius: 40px;
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
  transition: all .2s;
}

.button:hover {
  -webkit-box-shadow: var(--shadow);
  box-shadow: var(--shadow);
}

.button:active {
  -webkit-box-shadow: var(--shadow-active);
  box-shadow: var(--shadow-active);
}

@media (hover: none), (pointer: coarse) {
  .button:hover:not(:active) {
    box-shadow: none !important;
    -webkit-box-shadow: none !important;
    background-color: var(--font-primary) !important;
    color: var(--font-secondary) !important;
    transform: none !important;
  }
}

.touch-device .button:hover:not(:active) {
  box-shadow: none !important;
  -webkit-box-shadow: none !important;
  background-color: var(--font-primary) !important;
  color: var(--font-secondary) !important;
  cursor: default !important;
  transform: none !important;
}

.button--double {
  grid-column: span 2;
}

@media (width >= 744px) {
  .button--double {
    grid-column: 2 / -2;
  }
}

@media (width >= 1260px) {
  .button--double {
    grid-column: 5 / -5;
  }
}

.button--double {
  display: flex;
}

.button--double-link {
  flex: 1;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  display: flex;
}

.button--double-left {
  border-right: 1px solid var(--font-secondary);
  border-radius: 40px 0 0 40px;
  padding-inline: 10px;
  font-size: 14px;
  overflow: hidden;
}

@media (width >= 744px) {
  .button--double-left {
    font-size: 16px;
  }
}

.button--double-left:hover {
  transform-origin: 100%;
  color: var(--font-primary);
  border-right: 1px solid var(--font-primary);
  background-color: #0000;
  transform: scale(1.1);
}

@media (hover: none), (pointer: coarse) {
  .button--double-left:hover:not(:active) {
    box-shadow: none !important;
    -webkit-box-shadow: none !important;
    background-color: var(--font-primary) !important;
    color: var(--font-secondary) !important;
    transform: none !important;
  }
}

.touch-device .button--double-left:hover:not(:active) {
  box-shadow: none !important;
  -webkit-box-shadow: none !important;
  background-color: var(--font-primary) !important;
  color: var(--font-secondary) !important;
  cursor: default !important;
  transform: none !important;
}

.button--double-right {
  border-left: 1px solid var(--font-secondary);
  border-radius: 0 40px 40px 0;
  padding-inline: 10px;
  font-size: 14px;
  overflow: hidden;
}

@media (width >= 744px) {
  .button--double-right {
    font-size: 16px;
  }
}

.button--double-right:hover {
  transform-origin: 0;
  color: var(--font-primary);
  border-left: 1px solid var(--font-primary);
  background-color: #0000;
  transform: scale(1.1);
}

@media (hover: none), (pointer: coarse) {
  .button--double-right:hover:not(:active) {
    box-shadow: none !important;
    -webkit-box-shadow: none !important;
    background-color: var(--font-primary) !important;
    color: var(--font-secondary) !important;
    transform: none !important;
  }
}

.touch-device .button--double-right:hover:not(:active) {
  box-shadow: none !important;
  -webkit-box-shadow: none !important;
  background-color: var(--font-primary) !important;
  color: var(--font-secondary) !important;
  cursor: default !important;
  transform: none !important;
}

.button-close {
  border: 1px solid var(--font-primary);
  background-color: rgba(var(--font-primary), .2);
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  display: flex;
  transform: scale(.7);
}

@media (width >= 744px) {
  .button-close {
    transform: scale(1);
  }
}

.button-close:hover {
  cursor: pointer;
  background-color: #f006;
}

.button-close:after {
  content: "";
  background-color: var(--font-primary);
  width: 6px;
  height: 30px;
  display: block;
  transform: translateX(-50%)rotate(-45deg);
}

.button-close:before {
  content: "";
  background-color: var(--font-primary);
  width: 6px;
  height: 30px;
  display: block;
  transform: translateX(50%)rotate(45deg);
}

@media (hover: none), (pointer: coarse) {
  .button-close:hover:not(:active) {
    box-shadow: none !important;
    -webkit-box-shadow: none !important;
    background-color: var(--font-primary) !important;
    color: var(--font-secondary) !important;
    transform: none !important;
  }
}

.touch-device .button-close:hover:not(:active) {
  box-shadow: none !important;
  -webkit-box-shadow: none !important;
  background-color: var(--font-primary) !important;
  color: var(--font-secondary) !important;
  cursor: default !important;
  transform: none !important;
}

.no-wrap-text {
  white-space: nowrap;
}

.switcher {
  border: 2px solid var(--font-primary);
  border-radius: 50px;
  width: 48px;
  height: 24px;
  transition: background-color .3s, border-color .3s;
  display: flex;
}

@media (width >= 744px) {
  .switcher {
    width: 64px;
    height: 32px;
  }
}

.switcher__icon {
  transform-origin: center;
  width: 100%;
  height: 100%;
  transition: transform .3s;
  display: flex;
}

.switcher__icon--day {
  transform: translateX(calc(50% + 2px));
}

.switcher__img {
  transition: transform .3s;
  display: block;
  rotate: 180deg;
}

.switcher__img--day {
  transform: rotate(180deg);
}

.scroll-top {
  z-index: 1;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  justify-content: center;
  align-items: center;
  width: 52px;
  height: 52px;
  transition: opacity .3s, transform .3s, visibility .3s;
  display: flex;
  position: fixed;
  bottom: 10px;
  right: 10px;
  transform: translateY(20px);
}

@media (width >= 744px) {
  .scroll-top {
    width: 52px;
    height: 52px;
    bottom: 24px;
    right: 24px;
  }
}

.scroll-top:before, .scroll-top:after {
  content: "";
  background-color: var(--font-primary);
  width: 2px;
  height: 10px;
  transition: background-color .3s;
  position: absolute;
}

.scroll-top:before {
  transform-origin: 0 0;
  transform: translate(50%)rotate(45deg);
}

.scroll-top:after {
  transform-origin: 100% 0;
  transform: translate(-50%)rotate(-45deg);
}

.scroll-top.visible {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

:root {
  --bg-page: #1d1d1d;
  --bg-form: #f8f8fa;
  --bg-header: #1d1d1d;
  --bg-header-top: #1d1d1de6;
  --bg-dropdown: #b6b5ae80;
  --bg-compare: #1d1d1de6;
  --border-form: #d5d7de;
  --font-primary: #fff;
  --font-secondary: #1d1d1d;
  --font-contacts: #cecece;
  --placeholder: #7e7e83;
  --shadow: 1px 4px 16px 0px #fff6;
  --shadow-active: 1px 4px 16px 0px #ffffffa3;
  --bg-tth: linear-gradient(90deg, #0000 0%, #0009 25%, #000 50%, #0009 75%, #0000 100%);
  --bg-tth-line: linear-gradient(90deg, #fff0 0%, #fff9 25%, #fff 50%, #fff9 75%, #fff0 100%);
  --bg-dropdown-grad: linear-gradient(90deg, #1d1d1d 0%, #1d1d1d 50%, #1d1d1d00 100%);
  --bg-dropdown-grad-reverse: linear-gradient(-90deg, #1d1d1d 0%, #1d1d1d 50%, #1d1d1d00 100%);
}

html[data-theme="light"] {
  --bg-page: #b6b5ae;
  --bg-header: #c2c2c2;
  --bg-header-top: #b6b5aee6;
  --bg-form: #7e7e83;
  --bg-dropdown: #1d1d1d80;
  --bg-compare: #c2c2c280;
  --border-form: #1d1d1d;
  --font-primary: #1d1d1d;
  --font-secondary: #fff;
  --font-contacts: #545454;
  --placeholder: #1d1d1d;
  --shadow: 1px 4px 16px 0px #1d1d1d66;
  --shadow-active: 1px 4px 16px 0px #1d1d1da3;
  --bg-tth: linear-gradient(90deg, #fff0 0%, #fff9 25%, #fff 50%, #fff9 75%, #fff0 100%);
  --bg-tth-line: linear-gradient(90deg, #0000 0%, #0009 25%, #000 50%, #0009 75%, #0000 100%);
  --bg-dropdown-grad: linear-gradient(90deg, #b6b5ae 0%, #b6b5ae 50%, #b6b5ae00 100%);
  --bg-dropdown-grad-reverse: linear-gradient(-90deg, #b6b5ae 0%, #b6b5ae 50%, #b6b5ae00 100%);
}

html[data-theme="light"] .header {
  background-image: none !important;
}

html[data-theme="light"] .header__title {
  background-color: #c2c2c280;
  border-radius: 20px;
}

.body {
  min-width: 320px;
}

.main__wrapper {
  padding-inline: 20px;
}

@media (width >= 744px) {
  .main__wrapper {
    padding-inline: 72px;
  }
}

@media (width >= 1260px) {
  .main__wrapper {
    padding-inline: 120px;
  }
}

.main__wrapper {
  max-width: 1260px;
  margin-inline: auto;
  padding-top: 60px;
  padding-bottom: 120px;
}

@media (width >= 744px) {
  .main__wrapper {
    padding-top: 120px;
  }
}

@media (width >= 1260px) {
  .main__wrapper {
    padding-bottom: 182px;
  }
}

.nav__list {
  letter-spacing: 2px;
  flex-direction: column;
  font-size: 22px;
  line-height: 22px;
  display: flex;
}

@media (width >= 1260px) {
  .nav__list--header {
    flex-direction: row;
  }
}

.nav__item {
  display: flex;
}

.nav__link {
  z-index: 2;
  text-transform: uppercase;
  align-content: center;
  display: block;
  position: relative;
}

.nav__link:after {
  opacity: 0;
  content: "";
  background-color: var(--font-secondary);
  transform-origin: 0;
  width: 100%;
  height: 1px;
  transition: transform .3s;
  display: block;
  position: absolute;
  bottom: 0;
  transform: scale(0);
}

.nav__link:hover:after {
  opacity: 1;
  left: 0;
  transform: scale(1);
}

.dropdown {
  z-index: 1;
  background-color: var(--bg-dropdown);
  border-radius: 0 0 10px 10px;
  grid-template-columns: 100%;
  grid-auto-flow: row;
  row-gap: 20px;
  max-width: 800px;
  max-height: 536px;
  padding-block: 56px;
  display: grid;
  position: static;
  top: 56px;
  overflow: clip scroll;
}

.dropdown--croll {
  top: 92px;
}

@media (width >= 1260px) and (width <= 1450px) {
  .dropdown--croll {
    top: 56px;
  }
}

.dropdown:hover::-webkit-scrollbar {
  width: 5px;
}

.dropdown::-webkit-scrollbar {
  width: 0;
}

.dropdown::-webkit-scrollbar-track {
  background-color: #0000;
}

.dropdown::-webkit-scrollbar-thumb {
  background-color: rgba(var(--font-primary), .5);
  border-radius: 5px;
}

.dropdown-button {
  background-color: #0000;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  min-width: 36px;
  height: 36px;
  transition: all .2s ease-in-out;
  top: 0;
  left: 0;
}

.dropdown-button:hover {
  background-color: var(--bg-dropdown);
}

.dropdown-button:before, .dropdown-button:after {
  content: "";
  background-color: var(--font-primary);
  width: 4px;
  height: 15px;
}

.dropdown-button:before {
  transform-origin: 100% 100%;
  transform: rotate(45deg);
}

.dropdown-button:after {
  transform-origin: 0 100%;
  transform: rotate(-45deg);
}

.dropdown-button--open:before {
  transform-origin: 100% 0;
  transform: rotate(-45deg);
}

.dropdown-button--open:after {
  transform-origin: 0 0;
  transform: rotate(45deg);
}

.dropdown__item {
  background-color: var(--bg-page);
  color: var(--font-primary);
  font-size: 12px;
  font-weight: 800;
}

@media (width >= 1260px) {
  .dropdown__item {
    color: var(--font-secondary);
    background-color: #0000;
    font-size: 28px;
  }

  .dropdown__item:hover {
    background: var(--bg-dropdown-grad);
    color: var(--font-primary);
  }
}

.dropdown__link {
  flex-direction: column;
  grid-template-columns: 162px 1fr;
  grid-template-areas: "first last";
  gap: 10px;
  display: flex;
}

@media (width >= 744px) {
  .dropdown__link {
    align-items: center;
    display: grid;
  }
}

.dropdown__img {
  aspect-ratio: 1.5;
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: contain;
  background-color: var(--bg-page);
  height: 100%;
}

.dropdown__img--1 {
  background-image: url("bike_1--low.4d7dfbe3.webp");
}

.dropdown__img--2 {
  background-image: url("bike_2--low.0bb3ec8b.webp");
}

.dropdown__img--3 {
  background-image: url("bike_3--low.91d33d2c.webp");
}

.dropdown__img--4 {
  background-image: url("bike_4--low.a40eea9b.webp");
}

.dropdown__text {
  text-align: center;
  margin-bottom: 10px;
}

@media (width >= 744px) {
  .dropdown__text {
    margin-bottom: 0;
  }
}

.header {
  padding-inline: 20px;
}

@media (width >= 744px) {
  .header {
    padding-inline: 72px;
  }
}

@media (width >= 1260px) {
  .header {
    padding-inline: 120px;
  }
}

.header {
  padding-top: 24px;
  padding-bottom: 88px;
}

@media (width >= 744px) {
  .header {
    padding-top: 36px;
    padding-bottom: 130px;
  }
}

@media (width >= 1260px) {
  .header {
    padding-bottom: 203px;
  }
}

.header {
  background-color: var(--bg-header);
  background-image: url("header_bk--low.ba2f3c69.webp");
  background-position: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  flex-direction: column;
  justify-content: space-between;
  height: 480px;
  transition: background-color .3s;
  display: flex;
  position: relative;
  overflow: hidden;
}

@media (width >= 744px) {
  .header {
    background-position: center;
    height: 628px;
  }
}

@media (width >= 1260px) {
  .header {
    height: 100vh;
  }

  .header .icon-menu {
    display: none;
  }
}

.header__slider-container {
  position: absolute;
  inset: 0;
  overflow: hidden;
}

.header__slider-track {
  position: absolute;
  inset: 0;
}

.header__image {
  will-change: transform, background-position;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  inset: 0;
  transform: translateX(-100%);
}

.is-animating .header__image {
  transition: transform 1.5s ease-out, background-position 1.5s ease-out;
}

.header__image--current {
  background-position: 100%;
  transform: translateX(0);
}

.header__image--next {
  background-position: 100%;
  transform: translateX(-100%);
}

.is-animating .header__image--current {
  transform: translateX(-100%);
}

.is-animating .header__image--next {
  transform: translateX(0);
}

@media (width >= 744px) {
  .header__image {
    background-position: center;
    background-size: contain;
  }

  .header__image--current {
    transform: translateX(0);
  }

  .header__image--next {
    transform: translateX(-100%);
  }

  .is-animating .header__image--current {
    transform: translateX(100%);
  }

  .is-animating .header__image--next {
    transform: translateX(0);
  }
}

.header__image--1 {
  background-image: url("header_bike-fhd-1--low.999516a0.webp");
}

.header__image--2 {
  background-image: url("header_bike-fhd-2--low.9611820a.webp");
}

.header__image--3 {
  background-image: url("header_bike-fhd-3--low.ad251388.webp");
}

.header__image--4 {
  background-image: url("header_bike-fhd-4--low.f46f4dd2.webp");
}

.header__top {
  z-index: 4;
  grid-template: "logo list icons" 1fr
                 "center center center"
                 / 1fr 1fr 1fr;
  justify-content: space-between;
  align-items: center;
  padding-inline: 20px;
  display: grid;
  position: fixed;
  left: 0;
  right: 0;
}

@media (width >= 744px) {
  .header__top {
    padding-inline: 72px;
  }
}

@media (width >= 1260px) {
  .header__top {
    padding-inline: 120px;
  }
}

.header__top {
  opacity: 1;
  transition: transform .3s, opacity .3s;
  transform: translateY(0);
}

.header__top.hidden {
  opacity: 0;
  transform: translateY(-120%);
