}

.header__nav-contacts {
  margin-right: 20px;
}

.header__nav-link {
  height: 56px;
  color: var(--font-primary);
}

.header__nav-link:after {
  background-color: var(--font-primary);
}

.header__nav-link__dropdown--open {
