.header__details-dropdown--open {
  display: grid;
}

.header__list {
  flex-direction: row;
}

.header__icons {
  grid-column: 3 / -1;
  justify-content: end;
  gap: 24px;
  display: flex;
}

@media (width >= 744px) {
  .header__icons {
    gap: 32px;
  }
}

.header__title {
  z-index: 1;
  text-align: center;
  letter-spacing: -1.5px;
  width: 100%;
  font-size: 36px;
  font-weight: 700;
  line-height: 100%;
}

@media (width >= 744px) {
  .header__title {
    font-size: 64px;
