.compare__tth-weights {
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "left right";
  display: grid;
  position: relative;
}

.compare__tth-weights:before {
  content: "";
  background-color: var(--left-color, #9b2a2a00);
  height: calc(100% - 1px);
  width: var(--left-width, 0%);
  grid-area: left;
  transition: width .25s, background-color .25s;
  position: absolute;
  top: 0;
  right: 0;
}

.compare__tth-weights:after {
  content: "";
  background-color: var(--right-color, #17784c00);
  height: calc(100% - 1px);
  width: var(--right-width, 0%);
  grid-area: right;
  transition: width .25s, background-color .25s;
  position: absolute;
  top: 0;
  left: 0;
}

